//미디어 쿼리 적용 예정

//desktop (1920/1080~ )

.start-form {
  display: flex;
  // width: 1920px;
  // height: 1080px;
  width: 100%;
  height: 100%;
  padding: 309px 0px 48px 0px;
  justify-content: center;
  align-items: center;
  .start-logo {
    position: absolute;
    top: 309px;
    right: 50%;
    transform: translate(50%);
  }

  .start-title {
    position: absolute;
    bottom: 350px;
    color: #fff;
    font-family: Inter;
    font-size: 50px;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
  }
}
